import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import './_calendar.css';

import * as React from 'react';
import moment, { Moment } from 'moment';
import 'moment/locale/nb';
import { DayPickerSingleDateController } from 'react-dates';
import { AvailableDay } from '../../../domain/clinic/forces/types';
import LoadingIndicator from '../loadingIndicator/LoadingIndicator';
import { formatToAPIDate, dayIsBefore, numericDateToMoment } from '../../../utils/date.utils';
import styles from './Calendar.module.css';

interface State {
  calendarWidth: number;
  initialDate: number | null;
}
type Props = {
  handleSelection: (date: number) => void;
  handleMonthChange: (from: string, to: string) => void;
  date: number | null;
  availableDays: { [date: string]: AvailableDay };
  isLoading: boolean;
} & React.HTMLAttributes<HTMLElement>;

class Calendar extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.setUpMoment();

    this.state = {
      calendarWidth: 0,
      initialDate: null,
    };
    this.setCalendarSize = this.setCalendarSize.bind(this);
  }

  setUpMoment() {
    moment.updateLocale('nb', { weekdaysMin: 'S_M_T_W_T_F_S'.split('_') });
    moment().format('dd');
  }

  checkAndSetInitialDate() {
    if (this.props.date) {
      this.setState({
        initialDate: this.props.date,
      });
    }
  }

  componentDidMount() {
    this.setCalendarSize();
    this.checkAndSetInitialDate();
    window.addEventListener('resize', this.setCalendarSize);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.setCalendarSize);
  }

  componentDidUpdate(prevProps: Props) {
    const dateSet = !prevProps.date && this.props.date;
    if (dateSet) {
      this.setState({
        initialDate: this.props.date,
      });
    }
  }

  getInitialMonth(date: number | null) {
    let initialMonth = date ? () => numericDateToMoment(date) : null;
    return initialMonth;
  }

  isDayBlocked = (day: Moment) => {
    const previousDate = dayIsBefore(day);
    const unavailableDay = !this.isDayAvailable(day);
    return previousDate || unavailableDay;
  };

  isDayAvailable = (day: Moment) => {
    const dateFormattedToAPI = formatToAPIDate(day);
    return !!this.props.availableDays[dateFormattedToAPI];
  };

  render() {
    const date = this.props.date ? numericDateToMoment(this.props.date) : null;
    return (
      <div className={styles.calendarContainer}>
        <DayPickerSingleDateController
          date={date}
          focused={true}
          initialVisibleMonth={this.getInitialMonth(this.state.initialDate)}
          onDateChange={(newdate: Moment) => {
            this.props.handleSelection(newdate.valueOf());
          }}
          isDayBlocked={(d: Moment) => this.isDayBlocked(d)}
          onPrevMonthClick={(date: Moment) =>
            this.props.handleMonthChange(
              date.startOf('month').format('DD-MM-YYYY'),
              date.endOf('month').format('DD-MM-YYYY')
            )
          }
          onNextMonthClick={(date: Moment) =>
            this.props.handleMonthChange(
              date.startOf('month').format('DD-MM-YYYY'),
              date.endOf('month').format('DD-MM-YYYY')
            )
          }
          numberOfMonths={1}
          firstDayOfWeek={1}
          daySize={this.state.calendarWidth}
          monthFormat="MMMM"
          noBorder={true}
          navPrev={<div className={'prevButton'} />}
          navNext={<div className={'nextButton'} />}
        />

        {this.renderLoadingIfNeeded()}
      </div>
    );
  }

  renderLoadingIfNeeded() {
    if (!this.props.isLoading) {
      return null;
    }
    return (
      <div className={styles.loadingContainer}>
        <LoadingIndicator className={styles.loadingIndicator} />
      </div>
    );
  }

  setCalendarSize() {
    const width = window.innerWidth;
    let calendarSize = 0;
    if (width >= 1200) {
      calendarSize = 75;
    } else if (width >= 992) {
      calendarSize = 61;
    } else if (width >= 768) {
      calendarSize = 63;
    } else if (width >= 576) {
      calendarSize = 71;
    } else {
      calendarSize = Math.round(width / 7 - 9);
    }
    this.setState({ calendarWidth: calendarSize });
  }
}
export default Calendar;
