import * as React from 'react';
import { navigate } from 'gatsby';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { RootState } from '../../../root.reducer';
import {
  getSelectedTreatments,
  isBookingValid,
  isSubmitting,
  getTotalPrice,
  getTotalDuration,
  getSelectedDate,
  getSelectedTime,
  getSubmissionError,
  getUsePoints,
  getEditMode,
  getBooking,
  getCancelBookingError,
  getSelectedClinicId,
} from '../forces/selectors';
import {
  deselectTreatment,
  toggleUsePoints,
  bookAppointment,
  updateAppointment,
} from '../forces/actions';
import { cancelBooking } from '../../bookings/forces/actions';
import { Treatment } from '../../treatment/forces/types';
import BookingOverview from '../components/BookingOverview';
import FormAlert from '../../../common/components/form/FormAlert';
import CancelBooking from '../../user/components/CancelBooking';
import { getCurrentUser, isLoggedIn, isMember } from '../../user/forces/selectors';
import { getOlioPointsRounded } from '../../../utils/currency.utils';
import BOOKING_TEXTS from '../assets/texts';
import styles from './BookingOrderOverview.module.css';
import Routes from '../../../common/routing/Routes';

interface OwnProps {
  appointmentId: string;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

interface State {
  showCancelBookingModal: boolean;
}

class BookingOrderOverview extends React.Component<Props, State> {
  state: State = {
    showCancelBookingModal: false,
  };

  getPointsConfig() {
    const { isLoggedIn, currentUser, usePoints, toggleUsePoints } = this.props;
    if (!isLoggedIn || currentUser == null) {
      return null;
    }
    return {
      usePoints,
      currentAmount: getOlioPointsRounded(currentUser.points),
      onCheckboxChange: toggleUsePoints,
    };
  }

  toggleCancel() {
    this.setState({ showCancelBookingModal: !this.state.showCancelBookingModal });
  }

  async cancelBooking() {
    const { booking } = this.props;
    if (booking.id && booking.waitlistId) {
      const cancelBookingParams = {
        id: booking.id,
        waitlistId: booking.waitlistId,
      };
      try {
        await this.props.cancelBooking(cancelBookingParams);
        navigate(Routes.Bookings());
      } catch (err) {
        this.toggleCancel();
      }
    }
  }

  render() {
    const {
      selectedDate,
      selectedTime,
      totalPrice,
      totalDuration,
      selectedTreatments,
      selectedClinicId,
      bookAppointment,
      updateAppointment,
      isBookingValid,
      isSubmitting,
      submissionError,
      cancelBookingError,
      editMode,
      userIsMember,
    } = this.props;
    const showCancelBookingModal = this.state.showCancelBookingModal;

    const bookingButtonText = editMode ? BOOKING_TEXTS.BUTTONS.UPDATE : BOOKING_TEXTS.BUTTONS.BOOK;
    const callback = editMode ? updateAppointment : bookAppointment;
    const rightButton = {
      callback: callback,
      callbackText: bookingButtonText,
      validation: isBookingValid,
      isSubmitting: isSubmitting,
    };
    const cancelButton = editMode
      ? {
          callback: () => {
            this.toggleCancel();
          },
          callbackText: BOOKING_TEXTS.BUTTONS.CANCEL,
          validation: isBookingValid,
          isSubmitting: isSubmitting,
          isSecondary: true,
        }
      : undefined;
    const pointsConfig = this.getPointsConfig();
    return (
      <div className={styles.container}>
        <BookingOverview
          selectedDate={selectedDate}
          selectedTime={selectedTime}
          totalPrice={totalPrice}
          totalDuration={totalDuration}
          selectedTreatments={selectedTreatments}
          selectedClinicId={selectedClinicId}
          deselectTreatment={treatment => this.handleTreatmentDeselection(treatment)}
          dateTimeMessage={BOOKING_TEXTS.MESSAGES.MEET_UP_ON_TIME}
          rightButton={rightButton}
          cancelButton={cancelButton}
          pointsConfig={pointsConfig}
          userIsMember={userIsMember}
          editMode={editMode}
        />
        <FormAlert error={submissionError || cancelBookingError} />

        {showCancelBookingModal && (
          <CancelBooking
            isOpen={showCancelBookingModal}
            triggerCancel={() => {
              this.cancelBooking();
            }}
            toggleOpen={() => {
              this.toggleCancel();
            }}
            message={'Kansellerer du time vil du bli refundert i olio-poeng.'}
          />
        )}
      </div>
    );
  }

  handleTreatmentDeselection = (treatment: Treatment) => {
    const appointmentId = this.props.appointmentId;
    this.props.deselectTreatment({ treatment, appointmentId });
  };
}

const mapStateToProps = (state: RootState) => ({
  selectedTreatments: getSelectedTreatments(state),
  selectedClinicId: getSelectedClinicId(state),
  selectedDate: getSelectedDate(state),
  selectedTime: getSelectedTime(state),
  totalPrice: getTotalPrice(state),
  totalDuration: getTotalDuration(state),
  isSubmitting: isSubmitting(state),
  isBookingValid: isBookingValid(state),
  submissionError: getSubmissionError(state),
  currentUser: getCurrentUser(state),
  isLoggedIn: isLoggedIn(state),
  userIsMember: isMember(state),
  usePoints: getUsePoints(state),
  editMode: getEditMode(state),
  booking: getBooking(state),
  cancelBookingError: getCancelBookingError(state),
  state,
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      deselectTreatment,
      bookAppointment,
      updateAppointment,
      toggleUsePoints,
      cancelBooking,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(BookingOrderOverview);
