import * as React from 'react';
import classNames from 'classnames';
import FixedBarBottom from '../../../common/components/fixedBarBottom/FixedBarBottom';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import { IButtonState } from '../forces/types';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import styles from './BookingFixedBar.module.css';

type Props = {
  onNextButtonPressed: () => void;
  submitting: boolean;
  buttonState: IButtonState;
} & React.HTMLAttributes<HTMLElement>;

const BookingFixedBar: React.FunctionComponent<Props> = ({
  className,
  onNextButtonPressed,
  buttonState,
  submitting,
  ...props
}) => {
  return (
    <FixedBarBottom
      className={classNames(styles.container, className)}
      rightColumn={
        submitting ? (
          <LoadingIndicator />
        ) : (
          <CallToActionButton
            onClick={() => onNextButtonPressed()}
            styleType="secondary"
            disabled={!buttonState.isEnabled}
            className={styles.nextButton}>
            {buttonState.text}
          </CallToActionButton>
        )
      }
      {...props}
    />
  );
};

export default BookingFixedBar;
