import * as React from 'react';
import TreatmentSelection from '../../treatment/containers/TreatmentSelection';
import CustomerInfoForm from '../../customerInfo/containers/CustomerInfoForm';
import BookingOrderOverview from '../containers/BookingOrderOverview';
import { BookingSectionIDs, BookingSection, BookingContext } from '../forces/types';
import {
  hasSelectedTreatments,
  hasCustomerInfo,
  isBookingValid,
  hasDate,
  hasTime,
  hasPaymentInfo,
  hasSelectedClinic,
} from '../forces/selectors';
import CalendarSection from '../../calendar/containers/CalendarSection';
import TimeSelectionSection from '../../calendar/containers/TimeSelectionSection';
import BillingSection from '../../customerInfo/containers/BillingSection';
import { isUserGuest } from '../../user/forces/selectors';
import ClinicSection from '../../clinic/containers/ClinicSection';

const getBookingSections = (options: {
  appointmentId?: string;
  isLoggedIn: boolean;
  isUserGuest?: boolean;
  hideTreatmentSelection?: boolean;
  shouldShowBillingForm?: boolean;
  editingBooking?: boolean;
}): Array<BookingSection> => {
  const {
    appointmentId = '',
    isLoggedIn,
    hideTreatmentSelection = false,
    shouldShowBillingForm = false,
    editingBooking = false,
  } = options;

  let sections: BookingSection[] = [
    {
      id: BookingSectionIDs.TreatmentSelection,
      title: 'Velg en eller flere behandlinger',
      component: <TreatmentSelection appointmentId={appointmentId} />,
      type: 'secondary',
      sectionValidator: hasSelectedTreatments,
    },
    {
      id: BookingSectionIDs.ClinicSection,
      title: 'Velg lokasjon',
      component: <ClinicSection />,
      type: 'primary',
      sectionValidator: hasSelectedClinic,
    },
    {
      id: BookingSectionIDs.CalendarSection,
      title: 'Dato',
      component: <CalendarSection appointmentId={appointmentId} />,
      type: 'primary',
      className: '',
      sectionValidator: hasDate,
    },
    {
      id: BookingSectionIDs.TimeSection,
      component: <TimeSelectionSection />,
      type: 'tertiary',
      sectionValidator: hasTime,
    },
    {
      id: BookingSectionIDs.CustomerInfo,
      title: 'Detaljer',
      component: <CustomerInfoForm />,
      type: 'secondary',
      sectionValidator: hasCustomerInfo,
    },
    {
      id: BookingSectionIDs.BillingSection,
      title: 'Betalingsinfo',
      component: <BillingSection />,
      type: 'secondary',
      sectionValidator: hasPaymentInfo,
    },
    {
      id: BookingSectionIDs.Overview,
      component: <BookingOrderOverview appointmentId={appointmentId} />,
      type: 'secondary',
      sectionValidator: isBookingValid,
    },
  ];

  if (!shouldShowBillingForm) {
    sections = sections.filter(section => section.id != BookingSectionIDs.BillingSection);
  }
  if (hideTreatmentSelection) {
    sections = sections.filter(section => section.id != BookingSectionIDs.TreatmentSelection);
  }
  if (editingBooking) {
    sections = sections.slice(2, sections.length);
    sections[0].className = 'offsetTop';
  }
  if (!isLoggedIn) {
    // Return all sections if the user is not logged in
    return sections;
  }

  // Filter out the customer info section if the user is logged in
  return sections.filter(section => {
    return section.id != BookingSectionIDs.CustomerInfo;
  });
};

export default getBookingSections;
