import * as React from 'react';
import classNames from 'classnames';
import Scroll from 'react-scroll';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { RouterProps, RouteComponentProps, WindowLocation } from '@reach/router';
import BookingSection from '../components/BookingSection';
import PageTitle from '../../../common/components/pageTitle/PageTitle';
import BookingFixedBar from './BookingFixedBar';
import getBookingSections from '../components/bookingSections';
import { RootState } from '../../../root.reducer';
import { getAllTreatmentTypes } from '../../treatment/forces/actions';
import {
  resetBookingForm,
  prepopulateBookingWithQueryParameters,
  populateBookingWithExistingBooking,
  validateAndScrollBookingSections,
  bookAppointment,
  updateAppointment,
} from '../forces/actions';
import { shouldShowBillingForm, getBookingButtonState } from '../forces/selectors';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import {
  isTreatmentTypesLoading,
  isAllSelectedTreamentTypesHidden,
} from '../../treatment/forces/selectors';
import { isLoggedIn, isUserGuest } from '../../user/forces/selectors';
import Layout from '../../../common/components/layout/Layout';
import { getAvailability } from '../../clinic/forces/actions';

import styles from './Booking.module.css';

interface OwnProps {
  appointmentId?: string;
  id?: string;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  RouterProps &
  React.HTMLAttributes<HTMLElement> &
  RouteComponentProps;

interface State {
  loaded: boolean;
}

class Booking extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      loaded: false,
    };
  }

  async componentDidMount() {
    const { id } = this.props;
    if (!id) {
      this.setupNewBooking();
    } else {
      this.setupEditBooking(id);
    }
    this.props.getAllTreatmentTypes();
    this.props.prepopulateBookingWithQueryParameters(location ? location.search : null);
  }

  hasLoaded() {
    this.setState({ loaded: true });
  }

  setupNewBooking() {
    this.props.resetBookingForm();
    this.hasLoaded();
  }

  setupEditBooking(id: string) {
    this.props.resetBookingForm();
    this.props.populateBookingWithExistingBooking(id).then(() => {
      this.hasLoaded();
    });
  }

  renderLoading() {
    return <LoadingIndicator />;
  }

  renderContent() {
    const {
      validateAndScrollBookingSections,
      booking,
      bookingSections,
      bookingButton,
    } = this.props;

    const handleButton = () => {
      if (!bookingButton.isReady) {
        validateAndScrollBookingSections(bookingSections);
        return;
      }
      if (bookingButton.isUpdate) {
        this.props.updateAppointment();
      } else {
        this.props.bookAppointment();
      }
    };
    const submittingBooking = booking.submission.isLoading;
    return (
      <>
        {bookingSections.map(bookingSection => (
          <Scroll.Element key={bookingSection.id} name={bookingSection.id}>
            <BookingSection
              title={bookingSection.title}
              type={bookingSection.type}
              className={bookingSection.className ? bookingSection.className : ''}>
              {bookingSection.component}
            </BookingSection>
          </Scroll.Element>
        ))}
        <BookingFixedBar
          onNextButtonPressed={() => handleButton()}
          buttonState={bookingButton}
          submitting={submittingBooking}
        />
      </>
    );
  }

  render() {
    const { className, isTreatmentTypesLoading, hideTreatmentSelection, ...props } = this.props;

    const loading = isTreatmentTypesLoading && this.state.loaded;
    return (
      <Layout className={classNames(styles.container, className)} {...props}>
        <PageTitle
          className={hideTreatmentSelection ? styles.paddingBottom : undefined}
          title="Booking"
        />
        {loading && this.renderLoading()}
        {!loading && this.renderContent()}
      </Layout>
    );
  }
}

const mapStateToProps = (state: RootState, ownProps: OwnProps) => {
  const isUserLoggedIn = isLoggedIn(state);
  const hideTreatmentSelection = isAllSelectedTreamentTypesHidden(state);
  const bookingSections = getBookingSections({
    appointmentId: ownProps.appointmentId,
    isLoggedIn: isUserLoggedIn,
    hideTreatmentSelection,
    shouldShowBillingForm: shouldShowBillingForm(state),
    editingBooking: state.booking.editMode || ownProps.id ? true : false,
  });
  return {
    isTreatmentTypesLoading: isTreatmentTypesLoading(state),
    bookingButton: getBookingButtonState(state, bookingSections),
    booking: state.booking,
    bookingSections,
    hideTreatmentSelection,
  };
};

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      validateAndScrollBookingSections,
      bookAppointment,
      updateAppointment,
      getAvailability,
      resetBookingForm,
      prepopulateBookingWithQueryParameters,
      populateBookingWithExistingBooking,
      getAllTreatmentTypes,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(Booking);
