import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { PoseGroup } from 'react-pose';
import { RootState } from '../../../root.reducer';
import { setPaymentInformation } from '../../user/forces/actions';
import { BodyBolderText } from '../../../common/components/texts';
import { getBookingUI } from '../../booking/forces/selectors';
import { toggleShowAddedPaymentInfoSuccess } from '../../booking/forces/actions';
import PaymentContainer from '../../user/containers/PaymentContainer';
import SuccessAnimation from '../../../common/animations/SuccessAnimation';
import ContainerAnimation from '../../../common/animations/ContainerAnimation';
import styles from './BillingSection.module.css';

const TEXTS = {
  MAIN:
    'For  kunne booke som medlem, trenger vi betalingsinformasjonen din. Kortet ditt vil bli belastet kun nr du har booket time.',
};

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

interface State {
  hasUpdatedPaymentInfo: boolean;
}

class BillingSection extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      hasUpdatedPaymentInfo: false,
    };
  }

  render() {
    const { bookingUIConfig } = this.props;

    return (
      <div>
        <PoseGroup>
          {bookingUIConfig.showAddedPaymentInfoSuccess ? (
            <ContainerAnimation key="success">
              <SuccessAnimation />
            </ContainerAnimation>
          ) : (
            <ContainerAnimation key="form">
              <BodyBolderText className={styles.text}>{TEXTS.MAIN}</BodyBolderText>
              <PaymentContainer
                hideSkipButton
                buttonText="Submit"
                onSubmitCardToken={async cardToken => {
                  await this.props.setPaymentInformation({ cardToken });
                }}
                onPaymentSubmitted={() => {
                  this.props.toggleShowAddedPaymentInfoSuccess(true);
                }}
              />
            </ContainerAnimation>
          )}
        </PoseGroup>
      </div>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  bookingUIConfig: getBookingUI(state),
});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      setPaymentInformation,
      toggleShowAddedPaymentInfoSuccess,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(BillingSection);
