import React, { Component } from 'react';
import classNames from 'classnames';
import Infobox from '../../../common/components/infobox/Infobox';
import { Treatment } from '../forces/types';
import styles from './TreatmentSelectionButton.module.css';

type Props = {
  onClick: (treatment: Treatment, isSelected: boolean) => void;
  treatment: Treatment;
  isSelected: boolean;
};

class TreatmentSelectionButton extends Component<Props> {
  render() {
    const { treatment, isSelected } = this.props;
    const buttonStyle = isSelected ? styles.selected : styles.unselected;
    return (
      <button
        className={classNames(styles.button, buttonStyle)}
        onClick={() => this.props.onClick(treatment, isSelected)}>
        <div className={styles.buttonText}>{this.props.treatment.displayTitle}</div>
        <div className={styles.infobox}>
          <Infobox isSelected={isSelected} description={this.props.treatment.description} />
        </div>
      </button>
    );
  }
}

export default TreatmentSelectionButton;
