import * as React from 'react';
import { Elements, StripeProvider } from 'react-stripe-elements';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import { setPaymentInformation } from '../../user/forces/actions';
import NoSSR from '../../../common/components/ssr/NoSSR';
import config from '../../../config';
import StripeForm from '../../../common/components/stripe/StripeForm';

interface OwnProps {
  header?: JSX.Element;
  hideSkipButton?: boolean;
  buttonText?: string;
  existingLastFourCardDigits?: number;
  onSkipButtonClicked?: () => void;
  onPaymentSubmitted?: () => void;
  onSubmitCardToken: (cardToken: any) => Promise<any>;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

interface State {
  error: Error | null;
  isLoading: boolean;
}

class PaymentContainer extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isLoading: false,
      error: null,
    };
  }

  render() {
    return (
      <NoSSR>
        {this.props.header}
        <StripeProvider apiKey={config.stripePublishableKey}>
          <Elements>
            <StripeForm
              error={this.state.error}
              buttonText={this.props.buttonText}
              existingLastFourCardDigits={this.props.existingLastFourCardDigits}
              showLoader={this.state.isLoading}
              onTokenReceived={async cardToken => {
                try {
                  this.setState({ error: null, isLoading: true });
                  await this.props.onSubmitCardToken(cardToken);
                  this.setState({ error: null, isLoading: false });
                  this.props.onPaymentSubmitted && this.props.onPaymentSubmitted();
                } catch (err) {
                  this.setState({ error: err, isLoading: false });
                }
              }}
              hideSkipButton={this.props.hideSkipButton}
              onSkipButtonClicked={() => {
                this.props.onSkipButtonClicked && this.props.onSkipButtonClicked();
              }}
            />
          </Elements>
        </StripeProvider>
      </NoSSR>
    );
  }
}

const mapStateToProps = (state: RootState) => ({});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      setPaymentInformation,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(PaymentContainer);
