import * as React from 'react';
import classNames from 'classnames';
import { H2 } from '../../../common/components/texts';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import styles from './BookingSection.module.css';

type Props = {
  title?: string;
  type: 'primary' | 'secondary' | 'tertiary';
} & React.HTMLAttributes<HTMLElement>;

const BookingSection: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return (
    <section
      className={classNames(styles.container, styles[props.type], styles[className], className)}
      {...props}>
      <Container>
        <Row>
          <Column xs={12} mdOffset={2} md={8} lgOffset={3} lg={6}>
            {props.title && <H2 className={styles.title}>{props.title}</H2>}
            {props.children}
          </Column>
        </Row>
      </Container>
    </section>
  );
};

export default BookingSection;
