import * as React from 'react';
import { AdditionalField, Treatment } from '../../forces/types';
import AdditionalFieldsFactory from './AdditionalFieldsFactory';

type Props = {
  treatment: Treatment;
  additionalFields: Array<AdditionalField>;
} & React.HTMLAttributes<HTMLElement>;

const AdditionalFields: React.FunctionComponent<Props> = ({
  additionalFields,
  treatment,
  className,
  ...props
}) => {
  if (additionalFields.length === 0) {
    return null;
  }
  return (
    <>
      {additionalFields.map((additionalField, index) => (
        <AdditionalFieldsFactory
          key={index}
          treatment={treatment}
          additionalField={additionalField}
          className={className}
        />
      ))}
    </>
  );
};

export default AdditionalFields;
