import * as React from 'react';
import classNames from 'classnames';
import posed, { PoseGroup } from 'react-pose';
import { Treatment } from '../../treatment/forces/types';
import { BodyBolderText } from '../../../common/components/texts';
import styles from './BookingOverviewTreatmentsList.module.css';

const Item = posed.li();

type Props<T> = {
  treatments: Array<T>;
  hasRemoveButton: boolean;
  onRemoveTreatment?: (treatment: T) => void;
} & React.HTMLAttributes<HTMLElement>;

const BookingOverviewTreatmentsList: <T extends { id: string; title: string }>(
  props: Props<T>
) => React.ReactElement<Props<T>> = ({
  className,
  treatments,
  hasRemoveButton,
  onRemoveTreatment,
  ...props
}) => {
  return (
    <ul className={classNames(styles.list, className)} {...props}>
      <PoseGroup>
        {treatments.map((treatment, i) => {
          return (
            <Item key={treatment.id + i} className={styles.listItem}>
              <BodyBolderText>{treatment.title}</BodyBolderText>
              {hasRemoveButton && onRemoveTreatment && (
                <button
                  className={styles.removeButton}
                  onClick={() => onRemoveTreatment(treatment)}
                />
              )}
            </Item>
          );
        })}
      </PoseGroup>
    </ul>
  );
};

export default BookingOverviewTreatmentsList;
