import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import Calendar from '../../../common/components/calendar/Calendar';
import { RootState } from '../../../root.reducer';
import { getAvailableDates, isAvailableDatesLoading } from '../../clinic/forces/selectors';
import { getAvailability } from '../../clinic/forces/actions';
import {
  selectDate,
  scrollToBookingSection,
  setBookingSection,
} from '../../booking/forces/actions';
import {
  getSelectedDate,
  hasSelectedTreatments,
  hasSelectedClinic,
} from '../../booking/forces/selectors';
import { BodyText } from '../../../common/components/texts';
import { BookingSectionIDs } from '../../booking/forces/types';
import { Moment } from 'moment';
import {
  formatToAPIDate,
  getStartOfMonth,
  getEndOfMonth,
  isSelectedDateCurrentMonth,
  numericDateToMoment,
} from './../../../utils/date.utils';

interface OwnProps {
  appointmentId: string;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

class CalendarSection extends React.Component<Props> {
  componentDidUpdate(prevProps: Props) {
    const { selectedDate } = this.props;
    if (selectedDate && prevProps.selectedDate !== selectedDate) {
      const selectedDateSetFirstTime =
        !prevProps.selectedDate && prevProps.selectedDate !== selectedDate;
      this.checkAndGetAvailabilityByInitialDate(selectedDateSetFirstTime, selectedDate);
    }
  }

  checkAndGetAvailabilityByInitialDate(selectedDateSetFirstTime: boolean, selectedDate: number) {
    let selectedIsCurrentMonth = selectedDateSetFirstTime
      ? isSelectedDateCurrentMonth(selectedDate)
      : true;
    if (selectedDateSetFirstTime && !selectedIsCurrentMonth) {
      let momentDate = numericDateToMoment(selectedDate);
      this.changeMonth(momentDate);
    }
  }

  changeMonth(date: Moment) {
    const start = formatToAPIDate(getStartOfMonth(date));
    const end = formatToAPIDate(getEndOfMonth(date));
    this.handleMonthChange(start, end);
  }

  renderNoTreatmentsSelected() {
    return <BodyText>Velg behandling og lokasjon frst for  kunne velge dato</BodyText>;
  }

  scrollToNextSection = () => {
    this.props.setBookingSection(BookingSectionIDs.TimeSection);
    scrollToBookingSection(BookingSectionIDs.TimeSection);
  };

  handleDateSelection = (date: number) => {
    this.scrollToNextSection();
    this.props.selectDate(date);
  };

  handleMonthChange = (from: string, to: string) => {
    const appointmentId = this.props.appointmentId;
    this.props.getAvailability({ from, to, appointmentId });
  };

  getDate = (hasAvailableDates: boolean, selectedDate: number | null) => {
    return hasAvailableDates ? selectedDate : null;
  };

  render() {
    if (!this.props.hasSelectedTreatments || !this.props.hasSelectedClinic) {
      return this.renderNoTreatmentsSelected();
    }
    const date = this.getDate(this.props.availableDates ? true : false, this.props.selectedDate);
    const availableDays = this.props.availableDates ? this.props.availableDates.availableDays : {};
    const isLoading = this.props.isAvailableDatesLoading;
    return (
      <Calendar
        handleSelection={this.handleDateSelection}
        date={date}
        availableDays={availableDays}
        handleMonthChange={this.handleMonthChange}
        isLoading={isLoading}
      />
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  availableDates: getAvailableDates(state),
  selectedDate: getSelectedDate(state),
  isAvailableDatesLoading: isAvailableDatesLoading(state),
  hasSelectedTreatments: hasSelectedTreatments(state),
  hasSelectedClinic: hasSelectedClinic(state),
  state,
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      selectDate,
      getAvailability,
      setBookingSection,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(CalendarSection);
