import * as React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import { getSelectedClinicId, hasSelectedTreatments } from '../../booking/forces/selectors';
import { setWaitlistId, setBookingSection } from '../../booking/forces/actions';
import { BookingSectionIDs } from '../../booking/forces/types';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import config from '../../../config';
import styles from './ClinicSection.module.css';

enum Locations {
  vikatest = 'oliotest',
  valkyrientest = 'valkyrientest',
  vika = 'oliovika',
  valkyrien = 'valkyrien',
}

interface OwnProps {
  selectedWaitlistId?: string | null;
}

interface State {
  selectedWaitlistId: string | null;
}

type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

class ClinicSection extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
  }

  handleSelection = () => {
    this.props.setBookingSection(BookingSectionIDs.CalendarSection);
  };

  selectClinic = (clinicId: string) => {
    this.props.setWaitlistId(clinicId);
    this.handleSelection();
  };

  renderClinicButtons = (selectedClinicId: string | null) => {
    if (config.environment === 'development' || config.environment === 'staging') {
      return (
        <>
          <CallToActionButton
            styleType={selectedClinicId === Locations.vikatest ? 'secondary' : 'outlined'}
            theme="light"
            size="normal"
            onClick={() => this.selectClinic(Locations.vikatest)}>
            Vika
          </CallToActionButton>
          <CallToActionButton
            styleType={selectedClinicId === Locations.valkyrientest ? 'secondary' : 'outlined'}
            theme="light"
            size="normal"
            onClick={() => this.selectClinic(Locations.valkyrientest)}>
            Valkyrien
          </CallToActionButton>
        </>
      );
    } else {
      return (
        <>
          <CallToActionButton
            styleType={selectedClinicId === Locations.vika ? 'secondary' : 'outlined'}
            theme="light"
            size="normal"
            onClick={() => this.selectClinic(Locations.vika)}>
            Vika
          </CallToActionButton>
          <CallToActionButton
            styleType={selectedClinicId === Locations.valkyrien ? 'secondary' : 'outlined'}
            theme="light"
            size="normal"
            onClick={() => this.selectClinic(Locations.valkyrien)}>
            Valkyrien
          </CallToActionButton>
        </>
      );
    }
  };

  render() {
    const selectedClinicId = this.props.selectedClinicId ? this.props.selectedClinicId : null;
    const { booking } = this.props.state;
    return (
      <div className={styles.container}>
        <div className={styles.divider} />
        <div className={styles.locationButtonContainer}>
          {this.renderClinicButtons(selectedClinicId)}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  selectedClinicId: getSelectedClinicId(state),
  state,
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      setWaitlistId,
      setBookingSection,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(ClinicSection);
