import * as React from 'react';
import { Router, RouteComponentProps } from '@reach/router';
import { navigate } from 'gatsby';
import Routes from '../../../common/routing/Routes';
import Booking from '../containers/Booking';
import BookingSuccess from '../containers/BookingSuccess';
import PrivateRoute from '../../user/containers/PrivateRoute';
import { UserRole } from '../../user/forces/types';
import USER_TEXTS from '../../user/assets/texts';

const BookingPage = () => {
  return (
    <Router>
      <NotFoundRoute default />
      <Booking path="booking" />
      <Booking path="booking/:appointmentId" />
      <BookingSuccess path="booking/success" />
      <PrivateRoute
        roles={[UserRole.Guest, UserRole.Member]}
        path="booking/update/:id"
        component={Booking}
        message={USER_TEXTS.MESSAGES.LOGIN.TO_UPDATE_BOOKING}
      />
    </Router>
  );
};

const NotFoundRoute = (props: RouteComponentProps) => {
  navigate(Routes.Main());
  return null;
};

export default BookingPage;
