import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import * as styles from './TreatmentList.module.css';
import AdditionalTreatmentStyles from './additionalFields//AdditionalTreatment.module.css';
import { BodyBolderText } from '../../../common/components/texts';
import { TreatmentType, Treatment } from '../forces/types';
import TreatmentSelectionButton from './TreatmentSelectionButton';
import SectionListTitle from '../../../common/components/section/SectionListTitle';
import AdditionalFields from './additionalFields/AdditionalFields';
import { formatToNOK } from '../../../utils/currency.utils';
import AnimateHeight from '../../../common/components/animation/AnimateHeight';
import { RootState } from '../../../root.reducer';
import {
  isTreatmentSelected,
  hasSelectedAdditionalField,
  getTotalPriceOfAdditionalTreatments,
  numberOfSpecificTreatment,
} from '../../booking/forces/selectors';
import AddTreatments from './addTreatments/AddTreatments';

interface OwnProps {
  treatmentType: TreatmentType;
  handleSelection: (treatment: Treatment) => void;
  handleDeselection: (treatment: Treatment) => void;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

const TreatmentList: React.FunctionComponent<Props> = ({
  className,
  isTreatmentSelected,
  hasSelectedAdditionalField,
  getTotalPriceOfAdditionalTreatments,
  ...props
}) => {
  return (
    <div className={styles.container}>
      <SectionListTitle size="big" className={styles.typeHeader}>
        {props.treatmentType.title}
      </SectionListTitle>
      {props.treatmentType.treatments.map(treatment => {
        const isSelected = isTreatmentSelected(treatment) || hasSelectedAdditionalField(treatment);
        const totalPriceOfAdditionalTreatments = getTotalPriceOfAdditionalTreatments(treatment);
        return (
          <div key={treatment.id} className={styles.treatmentRow}>
            <div className={styles.buttonContainer}>
              <TreatmentSelectionButton
                onClick={(treatment, isSelected) => {
                  if (isSelected) {
                    props.handleDeselection(treatment);
                  } else {
                    props.handleSelection(treatment);
                  }
                }}
                isSelected={isSelected}
                treatment={treatment}
              />
              <AnimateHeight height={isSelected ? 'auto' : 0} className={styles.additionalFields}>
                <div className={AdditionalTreatmentStyles.container}>
                  <AdditionalFields
                    treatment={treatment}
                    additionalFields={treatment.additionalFields}
                  />
                  {treatment.multiple && (
                    <AddTreatments
                      treatment={treatment}
                      addTreatment={() => props.handleSelection(treatment)}
                      subtractTreatment={() => {
                        // Subtract selected treatment
                        props.handleDeselection(treatment);
                      }}
                      count={props.numberOfTreatments(treatment)}
                    />
                  )}
                </div>
              </AnimateHeight>
            </div>
            <BodyBolderText className={styles.treatmentPrice}>
              {totalPriceOfAdditionalTreatments}
            </BodyBolderText>
          </div>
        );
      })}
    </div>
  );
};

const mapStateToProps = (state: RootState) => ({
  numberOfTreatments: numberOfSpecificTreatment(state),
  isTreatmentSelected: isTreatmentSelected(state),
  hasSelectedAdditionalField: hasSelectedAdditionalField(state),
  getTotalPriceOfAdditionalTreatments: getTotalPriceOfAdditionalTreatments(state),
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators({}, dispatch);

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(TreatmentList);
