import React, { Component } from 'react';
import classNames from 'classnames';
import { injectStripe, ReactStripeElements } from 'react-stripe-elements';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';

import formFields, { STRIPE_FORM_FIELDS } from './fields/stripeFormFields';
import { RootState } from '../../../root.reducer';
import { getStripeToken } from '../../../domain/user/forces/actions';
import FormAlert from '../form/FormAlert';
import LoadingIndicator from '../loadingIndicator/LoadingIndicator';
import CallToActionButton from '../callToAction/CallToActionButton';
import Link from '../link/Link';
import styles from './StripeForm.module.css';

const formOptions = {
  style: {
    base: {
      color: '#69b2c3',
      letterSpacing: '0.025em',
      fontSize: '1.375rem',
      height: '45px !important',
      fontFamily: "'GT-Walsheim', Helvetica, sans-serif",
    },
    invalid: {
      color: '#f5634c',
    },
    complete: {
      color: '#69b2c3',
    },
  },
};

interface OwnProps {
  onTokenReceived: (cardToken: any) => void;
  onSkipButtonClicked?: () => void;
  showLoader?: boolean;
  hideSkipButton?: boolean;
  buttonText?: string;
  existingLastFourCardDigits?: number;
  error?: Error | null;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  React.HTMLAttributes<HTMLElement> &
  ReactStripeElements.InjectedStripeProps;

interface State {
  isLoading: boolean;
  error: Error | null;
}

class StripeForm extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      error: null,
      isLoading: false,
    };
  }

  renderFields: React.FunctionComponent = () => (
    <>
      {formFields.map(this.modifyPlaceholders).map((field, index) => {
        return (
          <div className={styles.container} key={index}>
            <field.Component
              className={classNames(styles.input, 'secondary')}
              placeholder={field.placeholder}
              {...formOptions}
            />
          </div>
        );
      })}
    </>
  );

  modifyPlaceholders = (formfield: any) => {
    if (
      this.props.existingLastFourCardDigits &&
      formfield.name === STRIPE_FORM_FIELDS.CARD_NUMBER
    ) {
      formfield.placeholder = `**** **** **** ${this.props.existingLastFourCardDigits}`;
    }
    return formfield;
  };

  async onSubmit(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    const { stripe, onTokenReceived, getStripeToken } = this.props;

    if (stripe == null) {
      throw new Error('Stripe is not loaded');
    }
    try {
      this.setState({ isLoading: true, error: null });
      const token = await getStripeToken(stripe);
      if (token == null) {
        throw new Error('Looks like the card information is wrong. Please try again');
      }
      this.setState({ isLoading: false, error: null });
      onTokenReceived(token);
    } catch (err) {
      this.setState({ error: err, isLoading: false });
    }
  }

  onSkipClicked(event: React.MouseEvent<HTMLAnchorElement>) {
    event.preventDefault();
    this.props.onSkipButtonClicked && this.props.onSkipButtonClicked();
  }

  render() {
    const { isLoading } = this.state;
    const { showLoader = false, hideSkipButton = false, buttonText = 'Neste' } = this.props;
    const showLoadingIndicator = showLoader || isLoading;
    const error = this.state.error || this.props.error;
    const Fields = this.renderFields;
    return (
      <form onSubmit={this.onSubmit.bind(this)}>
        <Fields />
        <CallToActionButton disabled={isLoading} type="submit" size="large">
          {buttonText}
        </CallToActionButton>
        {!hideSkipButton && (
          <Link
            underlined
            to="#"
            onClick={this.onSkipClicked.bind(this)}
            className={styles.skipButton}>
            Hopp over. Jeg gjr dette senere
          </Link>
        )}
        {showLoadingIndicator && <LoadingIndicator />}
        {error && <FormAlert error={error.message || 'Something went wrong'} />}
      </form>
    );
  }
}

const mapStateToProps = (state: RootState) => ({});
const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      getStripeToken,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(injectStripe(StripeForm));
