import * as React from 'react';
import * as styles from './TimeSelectionSection.module.css';
import TimeSelectionButton from '../components/TimeSelectionButton';
import { H3, BodyText } from '../../../common/components/texts';
import { TimeSlots, IGroupedTimeSlots, AvailableDatesResponse } from '../../clinic/forces/types';
import { formatToAPIDate, getStartOfDay, numericDateToMoment } from '../../../utils/date.utils';
import { getSelectedTime, getSelectedDate } from '../../booking/forces/selectors';
import {
  selectTime,
  scrollToBookingSection,
  setBookingSection,
} from '../../booking/forces/actions';
import { getAvailableDates, isAvailableDatesLoading } from '../../clinic/forces/selectors';
import { RootState } from '../../../root.reducer';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { BookingSectionIDs } from '../../booking/forces/types';
import { isLoggedIn } from '../../user/forces/selectors';
import { shouldShowBillingForm } from '../../booking/forces/selectors';

type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;

type Props = {
  handleTimeSelection: (time: number) => void;
  timeslots: TimeSlots[];
  selectedTime: number | null;
} & React.HTMLAttributes<HTMLElement> &
  DispatchProps &
  StateProps;

interface ITimeSectionComponentProps {
  header: string;
  timeSectionTimeSlots: TimeSlots[];
}
class TimeSelectionSection extends React.Component<Props> {
  timeSectionComponent: React.FunctionComponent<ITimeSectionComponentProps> = ({
    header,
    timeSectionTimeSlots,
  }) => {
    const noAvailableSlotInSection =
      timeSectionTimeSlots.length > 0 && !this.hasAvailableSlot(timeSectionTimeSlots);
    if (noAvailableSlotInSection) {
      return null;
    }
    return (
      <div className={styles.timeSection}>
        <div className={styles.header}>
          <H3>{header}</H3>
        </div>
        <div className={styles.timeSlotList}>
          {timeSectionTimeSlots.map(timeslot => {
            return (
              <TimeSelectionButton
                key={timeslot.time}
                handleSelection={(time: number) => this.handleTimeSelection(time)}
                time={timeslot.time}
                infoText={timeslot.info}
                available={timeslot.availability}
                selectedTime={this.props.selectedTime}
              />
            );
          })}
        </div>
      </div>
    );
  };

  scrollToNextSection = () => {
    const { shouldShowBillingForm, isLoggedIn } = this.props;
    const sectionIfLoggedIn = shouldShowBillingForm
      ? BookingSectionIDs.BillingSection
      : BookingSectionIDs.Overview;
    const nextSection = isLoggedIn ? sectionIfLoggedIn : BookingSectionIDs.CustomerInfo;

    this.props.setBookingSection(nextSection);
    scrollToBookingSection(nextSection);
  };

  handleTimeSelection = (time: number) => {
    this.scrollToNextSection();
    this.props.selectTime(time);
  };

  noTimeSlotsAvailableComponent: React.FunctionComponent = () => (
    <BodyText>
      Sorry! <br /> Ingen behandlinger er tilgjengelig for valgte datoen..
    </BodyText>
  );
  areTimeSlotsAvailable(groupedTimeSlots: IGroupedTimeSlots[]): boolean {
    return (
      groupedTimeSlots
        .filter(slotGroup => this.hasSlots(slotGroup.slots))
        .filter(slotGroup => this.hasAvailableSlot(slotGroup.slots)).length > 0
    );
  }
  hasSlots(timeslots: TimeSlots[]) {
    return timeslots.length != 0;
  }
  hasAvailableSlot(categorizedTimeslots: TimeSlots[]): boolean {
    return categorizedTimeslots.filter(timeSlot => timeSlot.availability > 0).length > 0;
  }

  mapTimeslotsToSection(slots: TimeSlots[]): IGroupedTimeSlots[] {
    const TIMES_IN_MS = {
      NOON: 43200000,
      AFTERNOON: 60300000,
    };
    const GROUPINGS = {
      MORNING: 'Morgen',
      AFTERNOON: 'Ettermiddag',
      EVENING: 'Kveld',
    };
    const morningSlots = slots.filter(timeslot => timeslot.time < TIMES_IN_MS.NOON);
    const afternoonSlots = slots.filter(
      timeslot => timeslot.time >= TIMES_IN_MS.NOON && timeslot.time < TIMES_IN_MS.AFTERNOON
    );
    const eveningSlots = slots.filter(timeslot => timeslot.time >= TIMES_IN_MS.AFTERNOON);
    return [
      { name: GROUPINGS.MORNING, slots: morningSlots },
      { name: GROUPINGS.AFTERNOON, slots: afternoonSlots },
      { name: GROUPINGS.EVENING, slots: eveningSlots },
    ];
  }

  getGroupedTimeSlots(
    availableDates: AvailableDatesResponse,
    selectedDate: number
  ): IGroupedTimeSlots[] {
    const dateToUse = formatToAPIDate(getStartOfDay(numericDateToMoment(selectedDate)));
    const slots = availableDates.availableDays[dateToUse].slots;
    return this.mapTimeslotsToSection(slots);
  }

  render() {
    const { selectedDate, availableDates } = this.props;
    const datesLoadingAndnoDatesAvailable =
      this.props.isAvailableDatesLoading || availableDates.firstAvailableDay == null;
    if (datesLoadingAndnoDatesAvailable) {
      return null;
    }
    if (!selectedDate) {
      return <BodyText>Du m velge en dato frst for  kunne velge time</BodyText>;
    } else if (!availableDates.availableDays[formatToAPIDate(selectedDate)]) {
      return null;
    }

    const categorizedTimeslots: IGroupedTimeSlots[] = this.getGroupedTimeSlots(
      availableDates,
      selectedDate
    );
    const TimeSectionComponent = this.timeSectionComponent;

    const timeSlotsAvailable = this.areTimeSlotsAvailable(categorizedTimeslots);
    const NoTimeSlotsAvailable = this.noTimeSlotsAvailableComponent;
    return (
      <div>
        {categorizedTimeslots.map(slotGroup => (
          <div key={slotGroup.name}>
            <TimeSectionComponent header={slotGroup.name} timeSectionTimeSlots={slotGroup.slots} />
          </div>
        ))}
        {!timeSlotsAvailable && <NoTimeSlotsAvailable />}
      </div>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  availableDates: getAvailableDates(state),
  selectedDate: getSelectedDate(state),
  selectedTime: getSelectedTime(state),
  isAvailableDatesLoading: isAvailableDatesLoading(state),
  isLoggedIn: isLoggedIn(state),
  shouldShowBillingForm: shouldShowBillingForm(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      selectTime,
      setBookingSection,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(TimeSelectionSection);
