import React from 'react';
import * as styles from './TimeSelectionButton.module.css';
import classNames from 'classnames';

type Props = {
  handleSelection: Function;
  time: number;
  selectedTime: number | null;
  available: number;
  infoText: string;
} & React.HTMLAttributes<HTMLElement>;

const TimeSelectionButton: React.ForwardRefExoticComponent<Props> = React.forwardRef<
  HTMLButtonElement,
  Props
>(({ className, infoText, ...props }, ref) => {
  const { available, time, selectedTime } = props;
  const isSelected = time === selectedTime;
  const isUnAvailable = available <= 0;

  const determineStyling = () => {
    if (isSelected) {
      return styles.selected;
    }
    if (isUnAvailable) {
      return styles.unavailable;
    }
    if (!isSelected) {
      return styles.unselected;
    }
  };
  let buttonStyle = determineStyling();
  const handleClick = () => (!isUnAvailable ? props.handleSelection(props.time) : null);
  const cssClassName = classNames(styles.button, buttonStyle);
  return (
    <button ref={ref} className={cssClassName} onClick={handleClick}>
      {infoText}
    </button>
  );
});

export default TimeSelectionButton;
