import * as React from 'react';
import classNames from 'classnames';
import AddTreatmentStyles from './AddTreatments.module.css';
import { Treatment } from '../../forces/types';
import CallToActionButton from '../../../../common/components/callToAction/CallToActionButton';
import styles from '../additionalFields/AdditionalTreatment.module.css';

type Props = {
  treatment: Treatment;
  addTreatment(): void;
  subtractTreatment(): void;
  count: number;
} & React.HTMLAttributes<HTMLElement>;

const SelectNumberOfTreatments: React.FunctionComponent<Props> = (props: Props) => {
  return (
    <div className={styles.containerInner}>
      <span>Antall behandlinger</span>
      <div className={AddTreatmentStyles.buttonContainer}>
        <CallToActionButton
          styleType="noline"
          className={classNames(AddTreatmentStyles.button)}
          theme="dark"
          onClick={() => {
            props.subtractTreatment();
          }}>
          -
        </CallToActionButton>
        <span className={AddTreatmentStyles.count}>
          {props.count ? props.count : 0 /* Count of amount specific treatment */}
        </span>
        <CallToActionButton
          styleType="noline"
          className={classNames(AddTreatmentStyles.button)}
          theme="dark"
          onClick={() => {
            props.addTreatment();
          }}>
          +
        </CallToActionButton>
      </div>
    </div>
  );
};

export default SelectNumberOfTreatments;
