import * as React from 'react';
import classNames from 'classnames';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { navigate } from 'gatsby';
import { H1, BodyText } from '../../../common/components/texts';
import CallToActionLink from '../../../common/components/callToAction/CallToActionLink';
import Routes from '../../../common/routing/Routes';
import { RootState } from '../../../root.reducer';
import { RouteComponentProps } from '@reach/router';
import { getSelectedTime, getSelectedDate, getEditMode } from '../forces/selectors';
import { formatToDateAndYear, formatDurationToTime } from '../../../utils/date.utils';
import Layout from '../../../common/components/layout/Layout';
import BookingOlioPointsGuestInformation from '../components/BookingOlioPointsGuestInformation';
import { Column, Container, Row } from '../../../common/components/grid/Grid';
import { isLoggedIn } from '../../user/forces/selectors';
import styles from './BookingSuccess.module.css';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps &
  StateProps &
  OwnProps &
  RouteComponentProps &
  React.HTMLAttributes<HTMLElement>;

class BookingSuccess extends React.Component<Props> {
  componentDidMount() {
    if (this.props.selectedDate == null || this.props.selectedTime == null) {
      navigate(Routes.Main());
    }
  }

  generateBookingText({ date, time, editMode }: { date: string; time: string; editMode: boolean }) {
    const baseText = {
      header: [''],
      body: `Vi gleder oss til  ta deg imot ${date} klokken ${''} ${time} :)`,
    };
    if (editMode) {
      baseText.header.push('Bookingen din er oppdatert.');
    }
    return baseText;
  }

  getTexts({
    selectedTime,
    selectedDate,
    editMode,
  }: {
    selectedTime: number;
    selectedDate: number;
    editMode: boolean;
  }) {
    const date = formatToDateAndYear(selectedDate);
    const time = formatDurationToTime(selectedTime);
    return this.generateBookingText({ date, time, editMode });
  }

  render() {
    const { className, selectedTime, selectedDate, isLoggedIn, editMode } = this.props;
    if (selectedDate == null || selectedTime == null) {
      return null;
    }
    const texts = this.getTexts({ selectedTime, selectedDate, editMode });
    return (
      <Layout className={classNames(styles.container, className)}>
        <Container>
          <Row>
            <Column xs={12} md={6} mdOffset={3} lg={4} lgOffset={4}>
              <H1 className={styles.title}>
                {texts.header[0]}
                <br />
                {texts.header[1]}
                <br />
                {texts.header[2]}
              </H1>
              <BodyText>{texts.body}</BodyText>

              {isLoggedIn ? null : (
                <BookingOlioPointsGuestInformation className={styles.guestInformation} />
              )}

              <CallToActionLink
                styleType="outlined"
                theme="dark"
                to={Routes.Main()}
                className={styles.homeButton}>
                Hjem
              </CallToActionLink>
            </Column>
          </Row>
        </Container>
      </Layout>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  editMode: getEditMode(state),
  selectedDate: getSelectedDate(state),
  selectedTime: getSelectedTime(state),
  isLoggedIn: isLoggedIn(state),
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators({}, dispatch);

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(BookingSuccess);
