import * as React from 'react';
import { AdditionalField, FieldTypes, Treatment } from '../../forces/types';
import { assertUnreachable } from '../../../../utils/ts.utils';
import AdditionalTreatment from './AdditionalTreatment';

type Props = {
  treatment: Treatment;
  additionalField: AdditionalField;
} & React.HTMLAttributes<HTMLElement>;

const AdditionalFieldsFactory: React.FunctionComponent<Props> = ({
  className,
  treatment,
  additionalField,
  ...props
}) => {
  switch (additionalField.type) {
    case FieldTypes.Treatment: {
      const additionalTreatment = additionalField as Treatment;
      return (
        <AdditionalTreatment
          parentTreatment={treatment}
          additionalTreatment={additionalTreatment}
          className={className}
        />
      );
    }
    case FieldTypes.Note:
      return null;
    default:
      assertUnreachable(additionalField.type);
      return null;
  }
};

export default AdditionalFieldsFactory;
