import * as React from 'react';
import classNames from 'classnames';
import * as styles from './Infobox.module.css';
import Tooltip from '../tooltip/Tooltip';

type Props = { description: string; isSelected: boolean } & React.HTMLAttributes<HTMLElement>;

const Infobox: React.FunctionComponent<Props> = ({ isSelected, ...props }) => {
  return (
    <div className={classNames(styles.infoSymbol, { [styles.infoSymbolSelected]: isSelected })}>
      <Tooltip className={styles.infoModal}>{props.description}</Tooltip>
    </div>
  );
};

export default Infobox;
