import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import TreatmentList from '../components/TreatmentList';
import { Treatment } from '../forces/types';
import { RootState } from '../../../root.reducer';
import {
  selectTreatment,
  deselectTreatment,
  setBookingSection,
  scrollToBookingSection,
} from '../../booking/forces/actions';
import { getTreatmentTypes } from '../forces/selectors';
import { BookingSectionIDs } from '../../booking/forces/types';
import { getBookingSectionScrollOffset } from '../../booking/forces/booking.utils';

interface OwnProps {
  appointmentId: string;
}

type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

class TreatmentSelection extends React.Component<Props> {
  render() {
    const { treatmentTypes } = this.props;
    return treatmentTypes.map(treatmentType => {
      if (treatmentType.hidden) return null;
      return (
        <TreatmentList
          key={treatmentType.id}
          treatmentType={treatmentType}
          handleSelection={this.handleTreatmentSelection}
          handleDeselection={this.handleTreatmentDeselection}
        />
      );
    });
  }

  setCurrentSection = () => {
    this.props.setBookingSection(BookingSectionIDs.TreatmentSelection);
  };

  handleSelection = () => {
    this.props.setBookingSection(BookingSectionIDs.CalendarSection);
  };

  handleTreatmentSelection = (treatment: Treatment) => {
    const appointmentId = this.props.appointmentId;
    this.props.selectTreatment({ treatment, appointmentId });
    this.handleSelection();
  };

  handleTreatmentDeselection = (treatment: Treatment) => {
    const appointmentId = this.props.appointmentId;
    this.props.deselectTreatment({ treatment, appointmentId });
    this.setCurrentSection();
  };
}

const mapStateToProps = (state: RootState) => ({
  treatmentTypes: getTreatmentTypes(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      selectTreatment,
      deselectTreatment,
      setBookingSection,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(TreatmentSelection);
