import React from 'react';
import { navigate } from 'gatsby';
import Routes from '../../../common/routing/Routes';
import { RouteComponentProps } from '@reach/router';
import { RootState } from '../../../root.reducer';
import { isLoggedIn, getCurrentUser } from '../forces/selectors';
import { connect } from 'react-redux';
import LoadingIndicator from '../../../common/components/loadingIndicator/LoadingIndicator';
import { isAppSetupLoading } from '../../app/forces/selectors';
import { UserRole } from '../forces/types';
import FormAlert from '../../../common/components/form/FormAlert';

interface OwnProps {
  roles?: Array<UserRole>;
  component: React.ComponentType<RouteComponentProps>;
  message?: string;
}
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = StateProps & OwnProps & React.HTMLAttributes<HTMLElement> & RouteComponentProps;

const PrivateRoute = ({
  component: Component,
  isLoggedIn,
  isAppSetupLoading,
  user,
  roles = [UserRole.Member],
  ...rest
}: Props) => {
  if (isAppSetupLoading) {
    return <LoadingIndicator />;
  }
  if (!isLoggedIn) {
    let options = {
      state: { message: rest.message },
    };
    navigate(Routes.LogIn(), options);
    return null;
  }
  if (user == null) {
    return null;
  }

  if (!roles.includes(user.role)) {
    return <FormAlert error={`You cannot access this as a ${user.role}`} />;
  }

  return <Component {...rest} />;
};

const mapStateToProps = (state: RootState) => ({
  isLoggedIn: isLoggedIn(state),
  isAppSetupLoading: isAppSetupLoading(state),
  user: getCurrentUser(state),
});

export default connect<StateProps, {}, OwnProps, RootState>(
  mapStateToProps,
  {}
)(PrivateRoute);
