import * as React from 'react';
import classNames from 'classnames';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { Treatment } from '../../forces/types';
import CallToActionButton from '../../../../common/components/callToAction/CallToActionButton';
import { RootState } from '../../../../root.reducer';
import {
  selectTreatment,
  selectAdditionalTreatment,
  deselectAdditionalTreatment,
} from '../../../booking/forces/actions';
import { isAdditionalFieldSelected } from '../../../booking/forces/selectors';
import styles from './AdditionalTreatment.module.css';

interface OwnProps {
  parentTreatment: Treatment;
  additionalTreatment: Treatment;
}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

const renderPriceIfNeeded = (parentTreatmentPrice: number, additionalTreatmentPrice: number) => {
  if (parentTreatmentPrice === additionalTreatmentPrice) {
    return null;
  }
  return `(+${additionalTreatmentPrice - parentTreatmentPrice})`;
};

const AdditionalTreatment: React.FunctionComponent<Props> = ({
  className,
  parentTreatment,
  additionalTreatment,
  selectTreatment,
  selectAdditionalTreatment,
  deselectAdditionalTreatment,
  isAdditionalFieldSelected,
  ...props
}) => {
  return (
    <div className={styles.containerInner}>
      {additionalTreatment.displayTitle}{' '}
      {renderPriceIfNeeded(parentTreatment.price, additionalTreatment.price)}
      <div className={styles.buttonContainer}>
        <CallToActionButton
          styleType="outlined"
          className={classNames(styles.button, {
            [styles.buttonSelected]: isAdditionalFieldSelected,
          })}
          theme="dark"
          onClick={() => {
            if (!isAdditionalFieldSelected) {
              selectAdditionalTreatment({ parentTreatment, additionalTreatment });
            }
          }}>
          Ja
        </CallToActionButton>
        <CallToActionButton
          styleType="outlined"
          theme="dark"
          className={classNames(styles.button, {
            [styles.buttonSelected]: !isAdditionalFieldSelected,
          })}
          onClick={() => {
            if (isAdditionalFieldSelected) {
              deselectAdditionalTreatment({ parentTreatment, additionalTreatment });
            }
          }}>
          Nei
        </CallToActionButton>
      </div>
    </div>
  );
};

const mapStateToProps = (state: RootState, { additionalTreatment }: OwnProps) => ({
  isAdditionalFieldSelected: isAdditionalFieldSelected(state, additionalTreatment),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      selectTreatment,
      selectAdditionalTreatment,
      deselectAdditionalTreatment,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(AdditionalTreatment);
