import * as React from 'react';
import Lottie from 'react-lottie';
import successAnimationJSON from './lottieSuccessAnimation.json';

type Props = {} & React.HTMLAttributes<HTMLElement>;

const SuccessAnimation: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return (
    <Lottie
      options={{
        autoplay: true,
        loop: false,
        animationData: successAnimationJSON,
      }}
      height={200}
      width={200}
    />
  );
};

export default SuccessAnimation;
