import * as React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import FormInput, { FormType } from '../../../common/components/form/FormInput';
import { RootState } from '../../../root.reducer';
import { getCustomerInfo } from '../../booking/forces/selectors';
import { updateCustomerInfo } from '../../booking/forces/actions';

interface OwnProps {}
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = DispatchProps & StateProps & OwnProps & React.HTMLAttributes<HTMLElement>;

interface FormFields {
  type: FormType;
  placeholder: string;
  customerInfoKey: string;
}
class CustomerInfoForm extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
  }

  private formFields: Array<FormFields> = [
    {
      type: 'name',
      placeholder: 'Navn *',
      customerInfoKey: 'name',
    },
    {
      type: 'tel',
      placeholder: 'Telefonnummer *',
      customerInfoKey: 'phoneNumber',
    },
    {
      type: 'email',
      placeholder: 'Epost *',
      customerInfoKey: 'email',
    },
  ];

  render() {
    const { updateCustomerInfo } = this.props;
    return (
      <form>
        {this.formFields.map(formField => (
          <FormInput
            key={formField.customerInfoKey}
            type={formField.type}
            placeholder={formField.placeholder}
            onChange={event =>
              updateCustomerInfo({ [formField.customerInfoKey]: event.target.value })
            }
            value={this.props.customerInfo[formField.customerInfoKey]}
          />
        ))}
      </form>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  customerInfo: getCustomerInfo(state),
});

const mapDispatchToProps = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      updateCustomerInfo,
    },
    dispatch
  );

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(CustomerInfoForm);
