import * as React from 'react';

import ConfirmActionModal from '../../../common/components/modal/ConfirmActionModal';
import { BodyText } from '../../../common/components/texts';

interface CancelBookingModalProps {
  isOpen: boolean;
  message: string;
  canceling: boolean;
  triggerCancel: () => void;
  toggleOpen: () => void;
}

const CancelBookingModal: React.FunctionComponent<CancelBookingModalProps> = ({
  isOpen,
  triggerCancel,
  canceling,
  toggleOpen,
  message,
}) => {
  return (
    <ConfirmActionModal
      isOpen={isOpen}
      showButtons={!canceling}
      handleResponse={response => {
        if (response) {
          triggerCancel();
        } else {
          toggleOpen();
        }
      }}>
      {message && <BodyText style={{ marginBottom: 'rem' }}>{message}</BodyText>}
    </ConfirmActionModal>
  );
};

export default CancelBookingModal;
