import React from 'react';
import H1 from '../texts/H1';
import CallToActionButton from '../callToAction/CallToActionButton';
import Modal from './Modal';
import styles from './ConfirmActionModal.module.css';

type OwnProps = {
  isOpen: boolean;
  showButtons: boolean;
  handleResponse: (response: boolean) => void;
};
type Props = OwnProps & React.HTMLAttributes<HTMLElement>;

const ConfirmActionModal: React.FunctionComponent<Props> = ({
  handleResponse,
  isOpen,
  showButtons = true,
  ...props
}) => {
  return (
    <Modal isOpen={isOpen} {...props}>
      <H1>Er du sikker?</H1>
      {props.children}
      <div className={styles.buttonContainer}>
        <CallToActionButton
          className={styles.button}
          styleType="secondary"
          onClick={() => handleResponse(true)}>
          Ja
        </CallToActionButton>
        <CallToActionButton
          className={styles.button}
          styleType="secondary"
          onClick={() => handleResponse(false)}>
          Nei
        </CallToActionButton>
      </div>
    </Modal>
  );
};

export default ConfirmActionModal;
