import * as React from 'react';
import ReactAnimateHeight from 'react-animate-height';

type Props = {
  height: number | 'auto';
} & React.HTMLAttributes<HTMLElement>;

const AnimateHeight: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return (
    <ReactAnimateHeight easing="ease" duration={300} height={props.height} className={className}>
      {props.children}
    </ReactAnimateHeight>
  );
};

export default AnimateHeight;
